document.addEventListener('DOMContentLoaded', () => {
    app();
})

function app() {
    checkbox();
    type();
    error();
}

function checkbox() {
    const checkbox = document.querySelector('.checkbox');
    const checked = document.querySelector('.checked');

    checkbox.addEventListener('click', () => {
        checked.classList.toggle('material-symbols-outlined');

        if (checked.classList.contains('material-symbols-outlined')) {
            checked.textContent = 'done';
        } else {
            checked.textContent = '';
        }
    })
}

function type() {
    const name = document.querySelector('#name');
    const botonContinue = document.querySelector('.boton-continue');

    botonContinue.addEventListener('click', (e) => {
        if (name.value != '') {
            window.location.replace("index.html");
        } else {
            const fieldText = document.querySelector('.field-text')

            const error = document.createElement('P');
            error.textContent = 'Type a name';

            fieldText.appendChild(error);

            name.classList.add('error-border')
        }
    })
}

function error() {
    const name = document.querySelector('#name');
    const botonContinue = document.querySelector('.boton-continue');
    name.addEventListener('input', (e) => {
        if (e.target.value != '') {
            botonContinue.classList.add('bg-red');
            name.classList.remove('error-border');

            name.nextElementSibling.remove()

        } else {
            botonContinue.classList.remove('bg-red');

        }
    });

    const cancel = document.querySelector('.boton-cancel');
    cancel.addEventListener('click', () => {
        window.location.replace("index.html");
    })
}